"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { User, Mail, Shield } from "lucide-react";

export default function ManageUsersPage() {
  const [users, setUsers] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await api.get("/users/all");
        setUsers(res.data);
      } catch (err) {
        console.error("Error fetching users", err);
      } finally {
        setLoading(false);
      }
    };

    fetchUsers();
  }, []);

  if (loading) return <p className="p-10 text-xl">Loading users...</p>;

  return (
    <div className="max-w-6xl mx-auto p-6">
      <h1 className="text-4xl font-bold text-black mb-8">
        Manage <span className="text-orange-600">Users</span>
      </h1>

      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {users.map((u) => (
          <div
            key={u.id}
            className="bg-white border shadow-md rounded-2xl p-6"
          >
            <h2 className="text-xl font-bold text-black flex items-center gap-2">
              <User className="text-orange-600" />
              {u.name}
            </h2>

            <p className="text-gray-700 mt-2 flex items-center gap-2">
              <Mail className="w-4 h-4" />
              {u.email}
            </p>

            <p className="mt-2 text-black">
              <strong>Country:</strong> {u.country || "N/A"}
            </p>

            <p className="mt-2 text-black flex items-center gap-2">
              <Shield className="w-4 h-4 text-orange-600" />
              <span
                className={`font-semibold ${
                  u.role === "admin"
                    ? "text-red-600"
                    : u.role === "manager"
                    ? "text-blue-600"
                    : "text-green-600"
                }`}
              >
                {u.role.toUpperCase()}
              </span>
            </p>

            <p className="mt-3 text-sm text-gray-500">
              Joined on {new Date(u.createdAt).toLocaleDateString()}
            </p>
          </div>
        ))}
      </div>
    </div>
  );
}
